import {
  getPendingSyncOperations,
  markSyncOperationComplete,
  markSyncOperationFailed,
  checkOnlineStatus,
  setupConnectivityListeners,
  getFromIndexedDB,
  saveToIndexedDB,
  clearIndexedDBStore
} from './offlineStorage';
import { supabase } from './supabase';
import { showInfo, showError } from '../components/common/Notification';

// Estado de la sincronizacin
let isSyncing = false;
let syncInterval = null;
const SYNC_INTERVAL = 30000; // 30 segundos

/**
 * Iniciar el servicio de sincronizacin
 * @param {Object} options - Opciones de configuracin
 * @param {boolean} options.showNotifications - Mostrar notificaciones
 * @param {number} options.interval - Intervalo de sincronizacin en ms
 * @returns {Object} - Mtodos para controlar la sincronizacin
 */
export const initSyncService = (options = {}) => {
  const {
    showNotifications = true,
    interval = SYNC_INTERVAL
  } = options;

  // Configurar listeners para cambios en el estado de la conexin
  setupConnectivityListeners(
    // Cuando se recupera la conexin
    () => {
      if (showNotifications) {
        showInfo('Conexin a Internet restablecida. Sincronizando datos...');
      }
      syncData();
    },
    // Cuando se pierde la conexin
    () => {
      if (showNotifications) {
        showInfo('Sin conexin a Internet. Los cambios se guardarn localmente.');
      }
    }
  );

  // Iniciar sincronizacin peridica
  if (syncInterval) {
    clearInterval(syncInterval);
  }

  syncInterval = setInterval(() => {
    if (checkOnlineStatus() && !isSyncing) {
      syncData();
    }
  }, interval);

  // Realizar sincronizacin inicial
  if (checkOnlineStatus()) {
    syncData();
  }

  // Devolver mtodos para controlar la sincronizacin
  return {
    syncNow: syncData,
    stopSync: () => {
      if (syncInterval) {
        clearInterval(syncInterval);
        syncInterval = null;
      }
    },
    resumeSync: () => {
      if (!syncInterval) {
        syncInterval = setInterval(() => {
          if (checkOnlineStatus() && !isSyncing) {
            syncData();
          }
        }, interval);
      }
    },
    getStatus: () => ({
      isSyncing,
      isOnline: checkOnlineStatus(),
      syncInterval: syncInterval ? interval : null,
    }),
  };
};

/**
 * Sincronizar datos con el servidor
 * @returns {Promise<Object>} - Resultado de la sincronizacin
 */
export const syncData = async () => {
  if (isSyncing || !checkOnlineStatus()) {
    return { success: false, message: 'Ya hay una sincronizacin en curso o no hay conexin' };
  }

  isSyncing = true;

  try {
    // Obtener operaciones pendientes
    const pendingOperations = await getPendingSyncOperations();

    if (pendingOperations.length === 0) {
      isSyncing = false;
      return { success: true, message: 'No hay operaciones pendientes', synced: 0 };
    }

    // Procesar operaciones pendientes
    const results = await Promise.allSettled(
      pendingOperations.map(processOperation)
    );

    // Contar resultados
    const succeeded = results.filter(r => r.status === 'fulfilled' && r.value.success).length;
    const failed = results.filter(r => r.status === 'rejected' || !r.value.success).length;

    isSyncing = false;

    return {
      success: failed === 0,
      message: `Sincronizacin completada: ${succeeded} operaciones exitosas, ${failed} fallidas`,
      synced: succeeded,
      failed,
      total: pendingOperations.length,
    };
  } catch (error) {
    console.error('Error durante la sincronizacin:', error);
    isSyncing = false;

    return {
      success: false,
      message: `Error durante la sincronizacin: ${error.message}`,
      synced: 0,
      failed: 0,
      total: 0,
    };
  }
};

/**
 * Procesar una operacin pendiente
 * @param {Object} operation - Operacin a procesar
 * @returns {Promise<Object>} - Resultado del procesamiento
 */
const processOperation = async (operation) => {
  try {
    let result;

    // Procesar segn el tipo de operacin
    switch (operation.type) {
      case 'CREATE':
        result = await handleCreateOperation(operation);
        break;
      case 'UPDATE':
        result = await handleUpdateOperation(operation);
        break;
      case 'DELETE':
        result = await handleDeleteOperation(operation);
        break;
      default:
        throw new Error(`Tipo de operacin desconocido: ${operation.type}`);
    }

    // Si la operacin fue exitosa, marcarla como completada
    if (result.success) {
      await markSyncOperationComplete(operation.id);
    } else {
      await markSyncOperationFailed(operation.id, result.error);
    }

    return result;
  } catch (error) {
    console.error(`Error al procesar operacin ${operation.id}:`, error);
    await markSyncOperationFailed(operation.id, error.message);

    return {
      success: false,
      error: error.message,
      operation,
    };
  }
};

/**
 * Manejar operacin de creacin
 * @param {Object} operation - Operacin a procesar
 * @returns {Promise<Object>} - Resultado del procesamiento
 */
const handleCreateOperation = async (operation) => {
  try {
    const { storeName, data } = operation;

    // Mapear nombre de almacn a tabla de Supabase
    const tableName = mapStoreNameToTable(storeName);

    // Insertar en Supabase
    const { data: result, error } = await supabase
      .from(tableName)
      .insert(data)
      .select();

    if (error) {
      throw error;
    }

    // Actualizar datos locales con los datos del servidor
    if (result && result.length > 0) {
      await saveToIndexedDB(storeName, result[0]);
    }

    return {
      success: true,
      result,
      operation,
    };
  } catch (error) {
    console.error('Error en operacin CREATE:', error);
    return {
      success: false,
      error: error.message,
      operation,
    };
  }
};

/**
 * Manejar operacin de actualizacin
 * @param {Object} operation - Operacin a procesar
 * @returns {Promise<Object>} - Resultado del procesamiento
 */
const handleUpdateOperation = async (operation) => {
  try {
    const { storeName, data } = operation;

    // Mapear nombre de almacn a tabla de Supabase
    const tableName = mapStoreNameToTable(storeName);

    // Actualizar en Supabase
    const { data: result, error } = await supabase
      .from(tableName)
      .update(data)
      .eq('id', data.id)
      .select();

    if (error) {
      throw error;
    }

    // Actualizar datos locales con los datos del servidor
    if (result && result.length > 0) {
      await saveToIndexedDB(storeName, result[0]);
    }

    return {
      success: true,
      result,
      operation,
    };
  } catch (error) {
    console.error('Error en operacin UPDATE:', error);
    return {
      success: false,
      error: error.message,
      operation,
    };
  }
};

/**
 * Manejar operacin de eliminacin
 * @param {Object} operation - Operacin a procesar
 * @returns {Promise<Object>} - Resultado del procesamiento
 */
const handleDeleteOperation = async (operation) => {
  try {
    const { storeName, data } = operation;

    // Mapear nombre de almacn a tabla de Supabase
    const tableName = mapStoreNameToTable(storeName);

    // Eliminar en Supabase
    const { error } = await supabase
      .from(tableName)
      .delete()
      .eq('id', data.id);

    if (error) {
      throw error;
    }

    return {
      success: true,
      operation,
    };
  } catch (error) {
    console.error('Error en operacin DELETE:', error);
    return {
      success: false,
      error: error.message,
      operation,
    };
  }
};

/**
 * Mapear nombre de almacn a tabla de Supabase
 * @param {string} storeName - Nombre del almacn
 * @returns {string} - Nombre de la tabla
 */
const mapStoreNameToTable = (storeName) => {
  // En este caso, los nombres coinciden, pero podra ser diferente
  return storeName;
};

/**
 * Sincronizar datos desde el servidor
 * @param {string} storeName - Nombre del almacn
 * @param {string} tableName - Nombre de la tabla
 * @param {Object} options - Opciones adicionales
 * @param {string} options.userId - ID del usuario para filtrar datos
 * @param {boolean} options.clearBeforeSync - Limpiar almacn antes de sincronizar
 * @returns {Promise<Object>} - Resultado de la sincronizacin
 */
export const syncFromServer = async (storeName, tableName = null, options = {}) => {
  try {
    if (!checkOnlineStatus()) {
      return { success: false, message: 'No hay conexin a Internet' };
    }

    // Si no se proporciona nombre de tabla, usar el nombre del almacn
    const table = tableName || mapStoreNameToTable(storeName);

    // Construir consulta
    let query = supabase.from(table).select('*');

    // Filtrar por usuario si se proporciona ID
    if (options.userId) {
      query = query.eq('user_id', options.userId);
    }

    // Obtener datos del servidor
    const { data: serverData, error } = await query;

    if (error) {
      throw error;
    }

    // Limpiar almacn si se solicita
    if (options.clearBeforeSync) {
      await clearIndexedDBStore(storeName);
    }

    // Guardar datos en IndexedDB
    if (serverData && serverData.length > 0) {
      await saveToIndexedDB(storeName, serverData);
      console.log(`Sincronizados ${serverData.length} registros de ${storeName} desde el servidor`);
    } else {
      console.log(`No se encontraron datos para sincronizar de ${storeName}`);
    }

    return {
      success: true,
      message: `Sincronizacin completada: ${serverData ? serverData.length : 0} registros`,
      count: serverData ? serverData.length : 0,
    };
  } catch (error) {
    console.error(`Error al sincronizar desde el servidor (${storeName}):`, error);
    return {
      success: false,
      message: `Error al sincronizar: ${error.message}`,
      error,
    };
  }
};

/**
 * Sincronizar todos los datos del usuario desde el servidor
 * @param {string} userId - ID del usuario
 * @returns {Promise<Object>} - Resultado de la sincronizacin
 */
export const syncAllUserData = async (userId) => {
  if (!userId) {
    console.error('No se proporcion ID de usuario para sincronizacin');
    return { success: false, message: 'No se proporcion ID de usuario' };
  }

  console.log(`Iniciando sincronizacin completa para el usuario ${userId}`);

  try {
    // Lista de almacenes a sincronizar
    const stores = [
      'transactions',
      'tasks',
      'events',
      'investments',
      'financial_goals'
    ];

    // Sincronizar cada almacn
    const results = await Promise.allSettled(
      stores.map(store =>
        syncFromServer(store, null, {
          userId,
          clearBeforeSync: true
        })
      )
    );

    // Contar resultados
    const succeeded = results.filter(r => r.status === 'fulfilled' && r.value.success).length;
    const failed = results.filter(r => r.status === 'rejected' || !r.value.success).length;

    return {
      success: failed === 0,
      message: `Sincronizacin completa: ${succeeded} almacenes sincronizados, ${failed} fallidos`,
      results: results.map((r, i) => ({
        store: stores[i],
        success: r.status === 'fulfilled' && r.value.success,
        message: r.status === 'fulfilled' ? r.value.message : r.reason.message
      }))
    };
  } catch (error) {
    console.error('Error durante la sincronizacin completa:', error);
    return {
      success: false,
      message: `Error durante la sincronizacin completa: ${error.message}`
    };
  }
};
